import React from 'react'
import styled from '@emotion/styled';
import { Layout, Typography, Button, Col, Row, Carousel, Timeline, Avatar, Space, Flex, Popover  } from 'antd';
import eeeimg from '../assets/card-eee.png';
import { PictureOutlined, LoadingOutlined, TrophyOutlined } from '@ant-design/icons';

const { Title, Paragraph } = Typography;
const { Content } = Layout;

const Container = styled.div`
    width: 100vw;
    padding: 25px;
    @media (min-width: 992px) {
      height: 100vh;
      padding: 50px;
    }
`;
export const EEE = () => {
  return (
    <Container id='EEE'>
      <Row align='middle'>
        <Col 
          xs={{ flex: '100%' }}
          md={{ flex: '50%' }}
          lg={{ flex: '25%' }}>
            <div style={{backgroundImage: `url(${eeeimg})`, backgroundSize: 'cover', width: '100%', height: 250, borderRadius: 20, backgroundPosition: 'center'}}></div>
            <Title style={{marginTop:-25, textAlign:'center'}} underline>EEE</Title>
            <Paragraph>
              The academic journey that I started with I want to learn programming but more, pushed me into the mysterious world of electronics. And here as I am exciting for more.
            </Paragraph>
        </Col>
        <Col 
          xs={{ flex: '100%' }}
          md={{ flex: '49%' }}>
          <Timeline mode='alternate'>
            <Timeline.Item label='2019'>Started his academic education.</Timeline.Item>
            <Timeline.Item label='2020'><Popover content="A musical instrument that plays with light, It was a design project.">Light Piano</Popover></Timeline.Item>
            <Timeline.Item label='2020'> <Popover content="An open-source macro keyboard project for people who needs more shortcuts (F13-F24).">MacrOUT</Popover></Timeline.Item>
            <Timeline.Item label='2021'> <Popover content="I got interest to micro and nano world of electronics in Nano and Micro Electromechanic Systems lecture.">MEMS</Popover> </Timeline.Item>
            <Timeline.Item label='2021'> <Popover content="Intensive Care Patient Tracking System: A medical instrument for monitoring patients' vital data.">ICPTS</Popover> </Timeline.Item>
            <Timeline.Item label='2023'> <Popover content="I designed a quadcopter with my classmates. It flies for over 30 minutes with a payload of over 10 kg.">Commercial purposed Quadcopter design</Popover></Timeline.Item>
            <Timeline.Item dot={<LoadingOutlined/>}>Can't wait to get his diploma!</Timeline.Item>
          </Timeline>
        </Col>
        <Col 
          xs={{ flex: '100%' }}
          md={{ flex: '100%' }}
          lg={{ flex: '25%' }}>
          <Space direction='vertical' align='center' style={{width:'100%'}}>
            <Avatar.Group shape='square' size={100} maxCount={2} maxPopoverTrigger="click">
              <Avatar>Light Piano</Avatar>
              <Avatar>MacrOUT</Avatar>
              <Avatar>ICPTS</Avatar>
            </Avatar.Group>
            <Button href='/portfolio?q=EEE'>See more</Button></Space>
        </Col>
        </Row>
    </Container>
  )
}
