import styled from '@emotion/styled';
import { Divider, Flex, Popover, Space, Tag, Typography } from 'antd';
import Tilt from 'react-parallax-tilt';
import holo from '../assets/holotexture.png';
import { PlusOutlined } from '@ant-design/icons';

const { Title } = Typography;

const Container = styled.div`
  width: 200px;
  height: 250px;
  border-radius: 20px;
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  -webkit-box-shadow: inset 0px 0px 37px 0px rgba(255,255,255,0.1);
  -moz-box-shadow: inset 0px 0px 37px 0px rgba(255,255,255,0.1);
  background: linear-gradient(115deg, rgb(211, 255, 215) 0%, rgb(0, 0, 0) 100%), radial-gradient(90% 100% at 50% 0%, rgb(200, 200, 200) 0%, rgb(22, 0, 45) 100%), radial-gradient(100% 100% at 80% 0%, rgb(250, 255, 0) 0%, rgb(36, 0, 0) 100%), radial-gradient(150% 210% at 100% 0%, rgb(112, 255, 0) 0%, rgb(20, 175, 125) 0%, rgb(0, 10, 255) 100%), radial-gradient(100% 100% at 100% 30%, rgb(255, 77, 0) 0%, rgba(0, 200, 255, 1) 100%), linear-gradient(60deg, rgb(255, 0, 0) 0%, rgb(120, 86, 255) 100%);
  background-blend-mode: overlay, overlay, difference, difference, difference, normal;
  cursor: pointer;
  @media (max-width: 510px) {
      min-width: 150px;
      width: calc(50vw - 20px);
      max-width: 200px;
  }
`;

const CardSoftware: React.FC = () => {
  return (
    <a href='#Software'>
    <Tilt
      glareEnable={true}
      glareMaxOpacity={0.45} 
      glareBorderRadius="20px"
    >
      <Container>
        <Space direction='vertical'>
        <h1 style={{color:'white', backgroundClip:'text',fontSize:'2em', fontFamily:'Syne', fontWeight:800}}>Software</h1>
        <Divider>as:</Divider>
        <Flex gap="4px 0" wrap="wrap" justify='center'>
          <Tag color='blue' bordered={false}>UI</Tag>
          <Tag color='gold' bordered={false}>Data</Tag>
          <Tag color='purple' bordered={false}>Eng.</Tag>
          <Popover
            content="You should take a look!"
          >
          <Tag style={{borderStyle:'dashed'}} icon={<PlusOutlined />}>and many more...</Tag></Popover>
        </Flex>
        </Space>
      </Container>
    </Tilt></a>
  );
};

export default CardSoftware;