import styled from '@emotion/styled';
import { Divider, Flex, Popover, Space, Tag, Typography } from 'antd';
import Tilt from 'react-parallax-tilt';
import holo from '../assets/holotexture.png';

const { Title } = Typography;

const Container = styled.div`
  width: 200px;
  height: 250px;
  border-radius: 20px;
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  -webkit-box-shadow: inset 0px 0px 37px 0px rgba(255,255,255,0.1);
  background: linear-gradient(120deg, #4B0000 0%, #000000 100%), linear-gradient(300deg, #1E0000 0%, #0038FF 100%, #0038FF 100%), linear-gradient(65deg, #00FFFF 0%, #FF00A8 100%), linear-gradient(185deg, #FF0000 10%, #1400FF 95%), radial-gradient(100% 140% at 100% 0%, #00A3FF 0%, #C10097 100%);
  background-blend-mode: color-dodge, overlay, difference, color-dodge, normal;
  cursor: pointer;
  @media (max-width: 510px) {
      min-width: 150px;
      width: calc(50vw - 20px);
      max-width: 200px;
  }
`;


const CardEEE: React.FC = () => {
  return (
    <a href='#EEE'>
    <Tilt
      glareEnable={true}
      glareMaxOpacity={0.45}
      glareBorderRadius="20px"
    >
      <Container>
        
        <Space direction='vertical'>
        <h1 style={{color:'white', backgroundClip:'text',fontSize:'2em', fontFamily:'Syne', fontWeight:800}}>EEE</h1>
        <Divider>as:</Divider>
        <Flex gap="4px 0" wrap="wrap" justify='center'>
          <Tag color='blue' bordered={false}>PCB</Tag>
          <Popover
            content="Nano and Micro Electromechanical Systems"
          >
          <Tag color='red' bordered={false}>MEMS</Tag></Popover>
          <Tag color='cyan' bordered={false}>Control Systems</Tag>
        </Flex>
        </Space>
      </Container>
    </Tilt></a>
  );
};

export default CardEEE;