import styled from '@emotion/styled';
import { Typography, Space, Divider, Flex, Tag } from 'antd';
import Tilt from 'react-parallax-tilt';
import holo from '../assets/holotexture.png';

const { Title } = Typography;


const Container = styled.div`
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
      background: linear-gradient(320.54deg, #00069F 0%, #120010 72.37%), linear-gradient(58.72deg, #69D200 0%, #970091 100%), linear-gradient(121.28deg, #8CFF18 0%, #6C0075 100%), linear-gradient(121.28deg, #8000FF 0%, #000000 100%), linear-gradient(180deg, #00FF19 0%, #24FF00 0.01%, #2400FF 100%), linear-gradient(52.23deg, #0500FF 0%, #FF0000 100%), linear-gradient(121.28deg, #32003A 0%, #FF4040 100%), radial-gradient(50% 72.12% at 50% 50%, #EB00FF 0%, #110055 100%);
      background-blend-mode: screen, color-dodge, color-burn, screen, overlay, difference, color-dodge, normal;
  -webkit-box-shadow: inset 0px 0px 37px 0px rgba(255,255,255,0.1);
  -moz-box-shadow: inset 0px 0px 37px 0px rgba(255,255,255,0.1);
      cursor: pointer;
      border-radius: 20px;
      width: 200px;
      height: 250px;
      padding: 25px;
      @media (max-width: 510px) {
          min-width: 150px;
          width: calc(50vw - 20px);
          max-width: 200px;
      }
`;

const CardDesign: React.FC = () => {
  return (
    <a href='#Design'>
    <Tilt
      glareBorderRadius="20px"
      glareEnable={true}
      glareMaxOpacity={0.45}
    >
      <Container>
        <Space direction='vertical'>
        <h1 style={{color:'white', backgroundClip:'text',fontSize:'2em', fontFamily:'Syne', fontWeight:800}}>Design</h1>
        <Divider>as:</Divider>
        <Flex gap="4px 0" wrap="wrap" justify='center'>
          <Tag color='blue' bordered={false}>UI</Tag>
          <Tag color='red' bordered={false}>Logo</Tag>
          <Tag color='cyan' bordered={false}>Font</Tag>
          <Tag color='purple' bordered={false}>Industrial</Tag>
        </Flex></Space>
      </Container>
    </Tilt></a>
  );
};

export default CardDesign;