import React from 'react'
import styled from '@emotion/styled';
import { Layout, Typography, Button, Col, Row, Carousel, Timeline, Avatar, Space, Flex, Popover, Badge  } from 'antd';
import artimg from '../assets/card-art.png';
import Nymph from '../assets/prf/art/nymph.png';
import same_enough from '../assets/prf/art/same_enough.png';
import vase from '../assets/prf/art/vase.jpg';
import { LoadingOutlined, TrophyOutlined, HighlightOutlined, CustomerServiceOutlined } from '@ant-design/icons';

const { Title, Paragraph } = Typography;

const Container = styled.div`
    width: 100vw;
    padding: 25px;
    @media (min-width: 768px) {
      height: 100vh;
      padding: 50px;
    }
`;

export const Art = () => {
  return (
    <Container id='Art'>
      <Row align='middle'>
        <Col
          xs={{ flex: '100%' }}
          md={{ flex: '50%' }}
          lg={{ flex: '25%' }}>
            <div style={{backgroundImage: `url(${artimg})`, backgroundSize: 'cover', width: '100%', height: 250, borderRadius: 20, backgroundPosition: 'center'}}></div>
            <Title style={{marginTop:-25, textAlign:'center'}} underline>Art</Title>
            <Paragraph>
            I have an interest in all forms of art. I enjoy drawing, painting, and creating digital art. I also love deal with sounds in free-time. I am always searching for new ways to express myself.
            </Paragraph>
        </Col>
        <Col
          xs={{ flex: '100%' }}
          md={{ flex: '49%' }}>
          <Timeline mode='alternate'>
            <Timeline.Item label='2001'><Popover content="Hello World!">Born</Popover></Timeline.Item>
            <Timeline.Item label='2007' dot={<CustomerServiceOutlined/>}><Popover content="It turned out I had a musical ear when spending time with the musician brother.">Musical skills</Popover></Timeline.Item>
            <Timeline.Item label='2010' dot={<TrophyOutlined/>} >2nd place in painting competition.</Timeline.Item>
            <Timeline.Item label='2019' dot={<HighlightOutlined/>} >"Nymph"</Timeline.Item>
            <Timeline.Item label='2021' dot={<HighlightOutlined/>} >"Same Enough"</Timeline.Item>
            <Timeline.Item label='2023' dot={<HighlightOutlined/>} >A wooden carved scarf tie.</Timeline.Item>
            <Timeline.Item label='2023' dot={<HighlightOutlined/>} >"Vase"</Timeline.Item>
            <Timeline.Item label='2023' dot={<HighlightOutlined/>} >"Self-portrait"</Timeline.Item>
            <Timeline.Item dot={<LoadingOutlined/>}>Loading inspiration...</Timeline.Item>
          </Timeline>
        </Col>
        <Col
          xs={{ flex: '100%' }}
          md={{ flex: '100%' }}
          lg={{ flex: '25%' }}>
          <Space direction='vertical' align='center' style={{width:'100%'}}>
            <Avatar.Group shape='square' size={{xs:70, md:70, lg:73, xl:90, xxl:90}} maxCount={2} maxPopoverTrigger="click">
              <Avatar src={Nymph} >Nymph</Avatar>
              <Avatar src={same_enough} >Same Enough</Avatar>
              <Avatar>Scarf Tie</Avatar>
              <Avatar src={vase}>Vase</Avatar>
              <Avatar>Self-Portrait</Avatar>
            </Avatar.Group>
            <Button href='/portfolio?q=Art'>See more</Button></Space>
        </Col>
        </Row>
    </Container>
  )
}