import styled from '@emotion/styled';
import { Card, Divider, Flex, Space, Tag, Typography } from 'antd';
import Tilt from 'react-parallax-tilt';
import holo from '../assets/holotexture.png';
import { m } from 'framer-motion';

const { Title, Text } = Typography;

var CardFont = '2em';

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

      background:
        conic-gradient(
          from 180deg at 50% 50%,
          rgba(0, 0, 0, 1) 0deg,
          rgba(255, 255, 255, 0.7) 17deg,
          rgba(0, 0, 0, 1) 88deg,
          rgba(255, 255, 255, 0.7) 152deg,
          rgba(0, 0, 0, 1) 225deg,
          rgba(255, 255, 255, 0.7) 289deg,
          rgba(0, 0, 0, 1) 360deg
        ),
        conic-gradient(
          from 180deg at 50% 50%,
          rgba(0, 0, 0, 1) 0deg,
          rgba(255, 255, 255, 1) 30deg,
          rgba(0, 0, 0, 1) 96deg,
          rgba(255, 255, 255, 1) 169deg,
          rgba(0, 0, 0, 1) 229deg,
          rgba(255, 255, 255, 1) 285deg,
          rgba(0, 0, 0, 1) 360deg
        ),
        radial-gradient(
          88% 127% at 13% 13%,
          rgba(248, 110, 251, 1) 8%,
          rgba(115, 66, 255, 1) 35%,
          rgba(66, 232, 255, 1) 63%,
          rgba(66, 255, 107, 1) 100%
        );
      background-blend-mode: screen, difference, normal;
      border-radius: 20px;
      width: 200px;
      height: 250px;
      padding: 25px;
      @media (max-width: 510px) {
          min-width: 150px;
          width: calc(50vw - 20px);
          max-width: 200px;
      }
`;
const CardArt: React.FC = () => {
  return (
    <a href='#Art'>
    <Tilt
      glareBorderRadius="20px"
      glareEnable={true}
      glareMaxOpacity={0.45}
    >
      <Container>
        <Space direction='vertical'>
        <h1 style={{color:'white', backgroundClip:'text',fontSize: `${CardFont}`, fontFamily:'Syne', fontWeight:800}}>Art</h1>
        <Divider>as:</Divider>
        <Flex gap="4px 0" wrap="wrap" justify='center'>
          <Tag color='geekblue' bordered={false}>Drawing</Tag>
          <Tag color='purple' bordered={false}>Music</Tag>
          <Tag color='lime' bordered={false}>3D</Tag>
          <Tag color='gold' bordered={false}>Woodwork</Tag>
        </Flex>
        </Space>
      </Container>
    </Tilt></a>
  );
};

export default CardArt;