import React from 'react'
import { ConfigProvider, Layout, Menu, Row, Space, Popover, Typography, theme } from 'antd';
import styled from '@emotion/styled';
import PronButton from '../components/PronButton';
import { Footer } from 'antd/es/layout/layout';

const { Title } = Typography;

const Container = styled.div`
    width: 100%;
    height: 100vh;
    background: hsla(169, 50%, 23%, 1);
    padding-top: 30vh;background: hsla(169, 33%, 22%, 1);

    background: linear-gradient(270deg, hsla(169, 33%, 22%, 1) 0%, hsla(171, 20%, 7%, 1) 100%);
    
    background: -moz-linear-gradient(270deg, hsla(169, 33%, 22%, 1) 0%, hsla(171, 20%, 7%, 1) 100%);
    
    background: -webkit-linear-gradient(270deg, hsla(169, 33%, 22%, 1) 0%, hsla(171, 20%, 7%, 1) 100%);
    
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#254A43", endColorstr="#0E1514", GradientType=1 );
`;

const LandingText = () => {
    const [hovered, setHovered] = React.useState(false);

    const getFontSize = () => {
        if (window.innerWidth >= 768) {
            return '5em';
        } else {
            return '8vw';
        }
    }

    return (
        <Container id='Home'>
            <Row justify="center" style={{ marginBottom: 0 }}>
                <Title level={4} style={{margin:0}}> Hello,</Title>
            </Row>
            <Row justify="center" style={{ marginTop: 0 }}>
                <Space direction='horizontal'>
                    <Title level={1} className="titlename" style={{ margin: 0, fontSize: getFontSize() }}>
                        It's&nbsp;
                    </Title>
                    <Popover
                        content={
                            <div style={{ fontFamily: 'sans-serif', fontWeight: 400 }}><PronButton />&nbsp; Hard to pronounce? Listen how it is. </div>}
                    >
                        <Title level={1} style={{ margin: 0, borderBottom: 'solid 1px white', fontSize: getFontSize() }} onMouseEnter={() => setHovered(true)} onMouseLeave={() => setHovered(false)}>{hovered ? "[œ:yt su kaɾaɟyn]" : "Öğüt Su Karagün"}</Title>
                    </Popover>
                </Space>
            </Row>
            <Row justify="center" style={{ marginBottom: 100 }}>
                <Title level={4} style={{ margin: 0 }}>
                    — 

                    <Popover
                        placement='bottom'
                        content={
                            <div style={{ fontFamily: 'sans-serif', fontWeight: 400 }}>  I've various things to love that, you will learn what they are. </div>}>
                        everything about me!
                    </Popover>
                </Title>
            </Row>
            
            <div className='field'>
		<div className="mouse"></div></div>
        </Container>
    )
}

export default LandingText