import React from 'react';
import sound from '../assets/pron.mp3';
import { Button } from 'antd';
import {CustomerServiceOutlined} from '@ant-design/icons';

const PronButton: React.FC = () => {
    const handleButtonClick = () => {
        const audio = new Audio(sound);
        audio.play();
    };

    return (
        <Button shape='circle' type='primary' ghost onClick={handleButtonClick} icon={<CustomerServiceOutlined/>} />
    );
};

export default PronButton;